.MenuWritten {
  padding: 0.5rem;
}

.MenuWritten__notice {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 130%;
  margin-bottom: 0.75rem;
}

.MenuWritten__size-group {
  background-color: var(--color-white);
  border: 0.1rem solid var(--color-dark-gray);
  border-radius: 0.75rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-bottom: 1.5rem;
  padding: 1rem;
  width: 100%;
}

.MenuWritten__title {
  font-family: 'Roboto Slab', serif;
  margin: 0;
  text-align: center;
}

.MenuWritten__subtitle {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 100;
  margin-top: 0;
  text-align: center;
}

.Ingredient--primary {
  font-size: 130%;
  font-weight: bold;
}

.Ingredient::after {
  content: '+';
  display: inline-block;
  font-weight: bold;
  margin: 0 0.5rem;
}

.Ingredient:last-child::after {
  display: none;
}



